import axios from 'axios';
import { View } from 'lucide-react';
import { useParams } from 'next/navigation';
import React, { useEffect, useState } from 'react'

const ViewProductDetails = () => {
    const {id}=useParams();
    const[product,setproduct]=useState(null);
    useEffect(()=>{
        if(id){
            axios.get(`http://localhost:5000/product/get/${id}`)
            .then((result) => {
                setproduct(result.data);
            }).catch((err) => {
                console.log(err);
                
            });
        }
    },[id]);
    if (!product) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-100">
        <p className="text-slate-600 text-lg font-medium">Loading product details...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-100 to-gray-200 px-6 py-12">
      <div className="relative bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl p-8 max-w-2xl w-full border border-slate-200">
        
        {/* Product Image */}
        <div className="relative">
          <img
            src={product.images}
            alt={product.name}
            className="w-full h-80 object-cover rounded-2xl shadow-md"
          />
          {/* Wishlist Icon */}
          <button className="absolute top-4 right-4 bg-white/80 p-2 rounded-full shadow hover:bg-red-100 transition">
            <Heart className="w-6 h-6 text-slate-700 hover:text-red-500" />
          </button>
        </div>

        {/* Product Info */}
        <div className="mt-6">
          <h1 className="text-3xl font-extrabold text-slate-800 mb-3">{product.name}</h1>
          <p className="text-lg text-slate-600 mb-4">{product.description}</p>

          <div className="grid grid-cols-2 gap-3 text-slate-700 text-sm mb-6">
            <div><span className="font-semibold">Brand:</span> {product.brand}</div>
            <div><span className="font-semibold">Category:</span> {product.category}</div>
            <div><span className="font-semibold">Stock:</span> {product.stock}</div>
            <div><span className="font-semibold">Tags:</span> {product.tags}</div>
          </div>

          {/* Price */}
          <div className="text-3xl font-bold text-slate-900 mb-6">â‚¹{product.price}</div>

          {/* Action Buttons */}
          <div className="flex gap-4">
            <button className="flex-1 bg-slate-800 text-white py-3 rounded-xl font-semibold shadow-md hover:bg-slate-900 transition">
              Add to Cart
            </button>
            <button className="flex-1 bg-blue-600 text-white py-3 rounded-xl font-semibold shadow-md hover:bg-blue-700 transition">
              Buy Now
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ViewProductDetails;
